import React from 'react';
import firebase from "firebase";

class FirebaseNews extends React.Component {
    state = {
        documents : [],
        isLoading: true
    }

    componentWillMount() {
        const db = firebase.firestore();
        db.collection('news').get().then((snapshot) => {
            let documents = [];
            snapshot.docs.forEach(doc => {
                let items = doc;
                documents.push(items)
            });

          this.setState({
              documents : documents,
              isLoading: false
            })

        });
    }

    render() {
        console.log(this.state.documents)
        if (this.state.isLoading) {
            return (
                <p>Ładowanie</p>
            )
        } else {
            return(
                <div>
                    <div className="firebase-add-btn"><i className="far fa-plus-square fa-5x"></i></div>
                    {this.state.documents.map(doc => {
                        return (
                            <div className="firebase-article" id={doc.id} key={doc.id}>
                                <div className="firebase-btns">
                                    <span className="firebase-edit-btn"><i className="far fa-edit fa-3x"></i></span>
                                    <span className="firebase-delete-btn"><i className="far fa-trash-alt fa-3x"></i></span>
                                </div>
                                <div className="article-details">
                                    <div className="title">Tytuł: {doc.data().title}</div>
                                    <div className="text">Kategoria: {doc.data().text}</div>
                                    <div className="img">{doc.data().img}</div>
                                </div>
                            </div>
                        )
                    })}
                </div>
            )
        }
    }
}

export default FirebaseNews;